#controls {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.container {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.input {
  border: 1px solid #f9a66c;
  border-radius: 5px;
}

.input:focus {
  border: 2px solid #f9a66c;
  outline: none;
}

.btn {
  color: #4a6163;
  background-color: #ffc94b;
  border: 1px solid #ffc94b;
  border-radius: 6px;
  padding: 10px 20px;
  font-weight: 600;
}

.btn:hover {
  background-color: #f9a66c;
  border: 1px solid #f9a66c;
}

#boxes {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

